import React, { useState, useEffect } from 'react'

import style from './style.module.scss'
import classNames from 'classnames/bind'

const cx = classNames.bind(style)

function LandingPage (props) {
  const [counter, setCounter] = useState(10)
  const [timerString, setTimerString] = useState('')

  useEffect(() => {
    counter > 0 && setTimeout(() => setCounter(counter - 1), 1000)
    setTimerString(new Date(counter * 1000).toISOString().substr(11, 8))
  }, [counter])

  const getFullCountdown = () => {
    return new Date(counter * 1000).toISOString().substr(11, 8)
  }

  const videoEnded = () => {
    console.log('video has ended')
    props.parentUpdate()
  }
  return (
    <div className={cx('landing-page')}>
      <div className={cx('landing-page__content')}>
        {(counter > 0) ? <div className={cx('content__timer', 'blink')}>{timerString}</div> : <div className={cx('content__timer')}>BEGIN</div>}
        {(counter === 0) && (
          <video onEnded={() => videoEnded()} className={cx('content__introvid')} controls>
            <source src='./intro.mp4' />
          </video>
        )}
      </div>
    </div>
  )
}

export default LandingPage
