import React, { useState, useRef } from 'react'
import Scene from './components/Scene'
import PasswordOverlay from './components/PasswordOverlay'
import LandingPage from './components/LandingPage'
import EndingPage from './components/EndingPage'


import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom"

import './App.css'

import { Canvas } from 'react-three-fiber'

function App () {
  const [showPasswordOverlay, setShowPasswordOverlay] = useState(true)
  const [showLandingPage, setShowLandingPage] = useState(false)
  const hidePasswordOverlay = () => {
    setShowPasswordOverlay(false)
    setShowLandingPage(true)
  }

  const hideLandingPage = () => {
    setShowLandingPage(false)
  }
  return (
    <div className='App'>
      <Router>
        <Switch>
          <Route path='/end'>
            <EndingPage />
          </Route>
          <Route path='/'>
            {showPasswordOverlay && <PasswordOverlay parentUpdate={() => hidePasswordOverlay()} /> }
            {showLandingPage && <LandingPage parentUpdate={() => hideLandingPage()} /> }
            <Canvas>
              <Scene />
            </Canvas>
          </Route>
        </Switch>
      </Router>
    </div>
  )
}

export default App
