import React, { useState, useEffect } from 'react'

import style from './style.module.scss'
import classNames from 'classnames/bind'

const cx = classNames.bind(style)

const Eye = (props) => (
  <svg className={cx('eye')} xmlns='http://www.w3.org/2000/svg' height='24' viewBox='0 0 24 24' width='24'><path d='M0 0h24v24H0z' fill='none'/><path d='M12 4.5C7 4.5 2.73 7.61 1 12c1.73 4.39 6 7.5 11 7.5s9.27-3.11 11-7.5c-1.73-4.39-6-7.5-11-7.5zM12 17c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z'/></svg>
)

const CODE = '011000110110100001101111011100100111010101110011'

function PasswordOverlay (props) {
  const [submitCode, setSubmitCode] = useState('')

  const toAscii = (input) => {
    let result = ''
    let arr = input.match(/.{1,8}/g)
    for (var i = 0; i < arr.length; i++) {
      result += String.fromCharCode(parseInt(arr[i], 2).toString(10))
    }
    return result
  }
  const validateCode = () => {
    console.log(submitCode, toAscii(CODE))
    if (submitCode.toLowerCase() === toAscii(CODE)) {
      props.parentUpdate()
    }
  }

  return (
    <div className={cx('password-overlay')}>
      <div className={cx('password-overlay__content')}>
        <label className={cx('content__label')}>ENTER THE CODE</label>
        <div className={cx('content__pwdwrap')}>
          <input className={cx('content__input')} onChange={event => setSubmitCode(event.target.value)} type='password' />
          <div className={cx('content__eyewrap')} onClick={() => validateCode()}><Eye /></div>
        </div>
      </div>
    </div>
  )
}

export default PasswordOverlay
