import {
  ShaderMaterial,
  UniformsUtils
} from 'three'
import { Pass } from 'three/examples/jsm/postprocessing/Pass.js'
import { CoverShader } from '../shaders/CoverShader.js'

class CoverTexturePass extends Pass {
  constructor (map, resolution) {
    super()

    const shader = CoverShader

    this.map = map
    this.resolution = resolution

    this.uniforms = UniformsUtils.clone(shader.uniforms)

    this.material = new ShaderMaterial({
      uniforms: this.uniforms,
      vertexShader: shader.vertexShader,
      fragmentShader: shader.fragmentShader,
      depthTest: false,
      depthWrite: false
    })

    this.needsSwap = false

    this.fsQuad = new Pass.FullScreenQuad(null)
  }

  render (renderer, writeBuffer, readBuffer /*, deltaTime, maskActive */) {
    const oldAutoClear = renderer.autoClear
    renderer.autoClear = false

    this.fsQuad.material = this.material

    this.uniforms.u_resolution.value = this.resolution
    this.uniforms.u_texture.value = this.map
    this.uniforms.u_tResolution.value = [this.map.image.width, this.map.image.height]

    renderer.setRenderTarget(this.renderToScreen ? null : readBuffer)

    if (this.clear) renderer.clear()
    this.fsQuad.render(renderer)

    renderer.autoClear = oldAutoClear
  }
}

export { CoverTexturePass }
