import React, { Suspense, useState, useRef, useMemo, useEffect } from 'react'
import PropTypes from 'prop-types'

import * as THREE from 'three'
import { useThree, useLoader, useFrame, extend } from 'react-three-fiber'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { TexturePass } from 'three/examples/jsm/postprocessing/TexturePass'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass'

import { CoverTexturePass } from '../../postprocessing/CoverTexturePass'
import { NoisyCirclePass } from '../../postprocessing/NoisyCirclePass'
import { NoisyCircleShader } from '../../shaders/NoisyCircleShader'
import { AlphaMaskShader } from '../../shaders/AlphaMaskShader'

import Box from '../Box'

import disp from './disp1.jpg'
import image1 from './img11.jpg'
import image2 from './img12.jpg'

extend({
  EffectComposer,
  CoverTexturePass,
  NoisyCirclePass,
  TexturePass,
  ShaderPass
})

// Render a scene with its own camera, and a plane that fills the entire camera
const TextureGenerator = ({ renderTarget, fragmentShader }) => {
  const { size } = useThree()
  const resolution = useMemo(() => [size.width, size.height], [size.height, size.width])

  const scene = useRef()
  const camera = useRef()

  const shaderMaterialArgs = {
    side: THREE.DoubleSide,
    uniforms: {
      u_timeElapsed: { type: 'f', value: 0.0 },
      u_mousePosition: { type: 'v2', value: [0, 0] },
      u_resolution: { type: 'v2', value: [0, 0] }
    },
    vertexShader: 'varying vec2 vUv;void main() {vUv = uv;gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );}',
    fragmentShader: fragmentShader
  }

  // const resolution = useMemo(() => [size.width, size.height], [size.height, size.width])
  const [mousePosition, setMousePosition] = useState([0, 0])
  const [timeElapsed, setTimeElapsed] = useState(0)
  useFrame(({ gl, clock, mouse }) => {
    setMousePosition([mouse.x, mouse.y])
    setTimeElapsed(clock.getElapsedTime())
    gl.setRenderTarget(renderTarget)
    gl.render(scene.current, camera.current)
    gl.setRenderTarget(null)
  })

  return (
    <scene ref={scene}>
      <orthographicCamera ref={camera} args={[-1, 1, 1, -1, 0, 1]} />
      <mesh>
        <planeBufferGeometry attach='geometry' args={[4, 4]} />
        <shaderMaterial attach='material' args={[shaderMaterialArgs]} uniforms-u_resolution-value={resolution} uniforms-u_timeElapsed-value={timeElapsed} uniforms-u_mousePosition-value={mousePosition}  />
      </mesh>
    </scene>
  )
}

TextureGenerator.propTypes = {
  renderTarget: PropTypes.object,
  fragmentShader: PropTypes.string
}

// EFFECTS
const Effects = ({ url }) => {
  const { gl, camera, size } = useThree()

  const resolution = useMemo(() => [size.width, size.height], [size.height, size.width])

  const maskRenderTarget = useMemo(() => {
    const parameters = {
      minFilter: THREE.LinearFilter,
      magFilter: THREE.LinearFilter,
      format: THREE.RGBAFormat,
      stencilBuffer: false
    }

    const pixelRatio = gl.getPixelRatio()
    const width = size.width
    const height = size.height

    return new THREE.WebGLRenderTarget(width * pixelRatio, height * pixelRatio, parameters)
  }, [])
  const composer = useRef()

  useEffect(() => {
    maskRenderTarget.setSize(size.width, size.height)
    composer.current.setSize(size.width, size.height)
  }, [size.width, size.height])

  const [mousePosition, setMousePosition] = useState([0, 0])
  const [timeElapsed, setTimeElapsed] = useState(0)
  useFrame((state) => {
    setMousePosition([state.mouse.x, state.mouse.y])
    setTimeElapsed(state.clock.getElapsedTime())
    gl.autoClear = false
    gl.clear()
    composer.current.render()
  })

  const texture = useLoader(THREE.TextureLoader, image1)

  return (
    <>
      <TextureGenerator renderTarget={maskRenderTarget} fragmentShader={NoisyCircleShader.fragmentShader} />
      <effectComposer ref={composer} args={[gl]}>
        <coverTexturePass attachArray='passes' args={[texture, resolution]} />
        <shaderPass attachArray='passes' args={[AlphaMaskShader]} uniforms-tMask-value={maskRenderTarget.texture} />
      </effectComposer>
    </>
  )
}

const Scene = (props) => {
  return (
    <Suspense fallback='Loading...'>
      <Effects />
    </Suspense>
  )
}

export default Scene
