var NoisyCircleShader = {
  uniforms: {
    u_timeElapsed: { type: 'f', value: 0 },
    u_mousePosition: { type: 'v2', value: null },
    u_resolution: { type: 'v2', value: null }
  },

  vertexShader: `
    varying vec2 vUv;
    void main() {

      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
    }
  `,

  fragmentShader: `
    uniform float u_timeElapsed;
    uniform vec2 u_mousePosition;
    uniform vec2 u_resolution;
    varying vec2 vUv;

    vec3 random3(vec3 c) {
      float j = 4096.0*sin(dot(c,vec3(17.0, 59.4, 15.0)));
      vec3 r;
      r.z = fract(512.0*j);
      j *= .125;
      r.x = fract(512.0*j);
      j *= .125;
      r.y = fract(512.0*j);
      return r-0.5;
    }

    const float F3 =  0.3333333;
    const float G3 =  0.1666667;
    float snoise(vec3 p) {
    
      vec3 s = floor(p + dot(p, vec3(F3)));
      vec3 x = p - s + dot(s, vec3(G3));
  
      vec3 e = step(vec3(0.0), x - x.yzx);
      vec3 i1 = e*(1.0 - e.zxy);
      vec3 i2 = 1.0 - e.zxy*(1.0 - e);
  
      vec3 x1 = x - i1 + G3;
      vec3 x2 = x - i2 + 2.0*G3;
      vec3 x3 = x - 1.0 + 3.0*G3;
  
      vec4 w, d;
  
      w.x = dot(x, x);
      w.y = dot(x1, x1);
      w.z = dot(x2, x2);
      w.w = dot(x3, x3);
  
      w = max(0.6 - w, 0.0);
  
      d.x = dot(random3(s), x);
      d.y = dot(random3(s + i1), x1);
      d.z = dot(random3(s + i2), x2);
      d.w = dot(random3(s + 1.0), x3);
  
      w *= w;
      w *= w;
      d *= w;
  
      return dot(d, vec4(52.0));
    }

    float circle(in vec2 st, in vec2 center, float radius){
      vec2 dist = st - center;
      dist.x = dist.x * (u_resolution.x / u_resolution.y);

      return smoothstep(radius, radius + 0.0001, length(dist));
    }
  
    void main() {
      vec2 st = gl_FragCoord.xy/u_resolution;

      float f = 0.;
      float t = u_timeElapsed * 0.5;
      vec2 uv = st;
      uv *= 5.0;
      mat2 m = mat2( 1.6,  1.2, -1.2,  1.6 );
      f  = 0.5000*snoise( vec3(uv, t) ); uv = m*uv;
      f += 0.2500*snoise( vec3(uv, t) ); uv = m*uv;
      f += 0.1250*snoise( vec3(uv, t) ); uv = m*uv;
      f += 0.0625*snoise( vec3(uv, t) ); uv = m*uv;

      vec2 displaced = st + vec2(f * 0.3);  
      vec2 mousePos = (u_mousePosition + 1.) / 2.;
      float radius = 0.2;
      vec3 color = vec3(circle(displaced, mousePos, radius));

      gl_FragColor = vec4(color, 1.);
    }
  `
}

export { NoisyCircleShader }
