var CoverShader = {
  uniforms: {
    u_texture: { value: null },
    u_resolution: { type: 'v2', value: null },
    u_tResolution: { type: 'v2', value: null }
  },

  vertexShader: `
    varying vec2 vUv;
    void main() {

      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
    }
  `,

  fragmentShader: `
    uniform vec2 u_resolution;
    uniform vec2 u_tResolution;
    uniform sampler2D u_texture;
    
    varying vec2 vUv;

    void main() {
      vec2 ratio = vec2(
        min((u_resolution.x / u_resolution.y) / (u_tResolution.x / u_tResolution.y), 1.0),
        min((u_resolution.y / u_resolution.x) / (u_tResolution.y / u_tResolution.x), 1.0)
      );
    
      vec2 uv = vec2(
        vUv.x * ratio.x + (1.0 - ratio.x) * 0.5,
        vUv.y * ratio.y + (1.0 - ratio.y) * 0.5
      );

      vec4 image = texture2D(u_texture, uv);

      gl_FragColor = image;
    }
  `
}

export { CoverShader }
